import React, { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { deliveryAPI } from '../services/deliveryAPI';
import { PARCEL_SIZES, DELIVERY_MODES, DEMO_USERS } from '../utils/constants';
import { calculatePrice, formatSYP, validateDeliveryForm } from '../utils/helpers';

const SendPackage = () => {
    const navigate = useNavigate();
    const [formData, setFormData] = useState({
        senderId: DEMO_USERS.SENDER.id,
        parcelSize: '',
        deliveryMode: '',
        pickupAddress: '',
        deliveryAddress: '',
        description: '',
        pickupInstructions: '',
        deliveryInstructions: ''
    });
    const [errors, setErrors] = useState({});
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [estimatedPrice, setEstimatedPrice] = useState(0);

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({
            ...prev,
            [name]: value
        }));

        if (name === 'parcelSize' && value) {
            const price = calculatePrice({ ...formData, [name]: value });
            setEstimatedPrice(price);
        }

        if (errors[name]) {
            setErrors(prev => ({
                ...prev,
                [name]: ''
            }));
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        const validation = validateDeliveryForm(formData);
        if (!validation.isValid) {
            setErrors(validation.errors);
            return;
        }

        setIsSubmitting(true);
        try {
            const delivery = await deliveryAPI.createDelivery(formData);
            alert(`    !\n : ${delivery.delivery_code}`);
            navigate('/deliveries');
        } catch (error) {
            alert('    : ' + error.message);
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <div className="form-container">
            <div className="navigation">
                <Link to="/">  </Link>
                <Link to="/deliveries"> </Link>
            </div>

            <h2>   </h2>
            <p>     </p>

            <form onSubmit={handleSubmit}>
                <div className="form-group">
                    <label>  *</label>
                    <select
                        name="parcelSize"
                        value={formData.parcelSize}
                        onChange={handleInputChange}
                        required
                    >
                        <option value="">  </option>
                        <option value={PARCEL_SIZES.SMALL}> ( 1 )</option>
                        <option value={PARCEL_SIZES.MEDIUM}> ( 5 )</option>
                        <option value={PARCEL_SIZES.LARGE}> ( 15 )</option>
                        <option value={PARCEL_SIZES.EXTRA_LARGE}>  ( 30 )</option>
                        <option value={PARCEL_SIZES.CUSTOM}> ( )</option>
                    </select>
                    {errors.parcelSize && <span style={{ color: 'red' }}>{errors.parcelSize}</span>}
                </div>

                <div className="form-group">
                    <label>  *</label>
                    <select
                        name="deliveryMode"
                        value={formData.deliveryMode}
                        onChange={handleInputChange}
                        required
                    >
                        <option value="">  </option>
                        <option value={DELIVERY_MODES.DOOR_TO_DOOR}>   </option>
                        <option value={DELIVERY_MODES.SHOP_TO_DOOR}>   </option>
                        <option value={DELIVERY_MODES.DOOR_TO_SHOP}>   </option>
                        <option value={DELIVERY_MODES.SHOP_TO_SHOP}>   </option>
                    </select>
                    {errors.deliveryMode && <span style={{ color: 'red' }}>{errors.deliveryMode}</span>}
                </div>

                <div className="form-group">
                    <label>  *</label>
                    <input
                        type="text"
                        name="pickupAddress"
                        value={formData.pickupAddress}
                        onChange={handleInputChange}
                        placeholder="   "
                        required
                    />
                    {errors.pickupAddress && <span style={{ color: 'red' }}>{errors.pickupAddress}</span>}
                </div>

                <div className="form-group">
                    <label>  *</label>
                    <input
                        type="text"
                        name="deliveryAddress"
                        value={formData.deliveryAddress}
                        onChange={handleInputChange}
                        placeholder="   "
                        required
                    />
                    {errors.deliveryAddress && <span style={{ color: 'red' }}>{errors.deliveryAddress}</span>}
                </div>

                <div className="form-group">
                    <label> </label>
                    <textarea
                        name="description"
                        value={formData.description}
                        onChange={handleInputChange}
                        placeholder="   ()"
                        rows="3"
                    />
                </div>

                <div className="form-group">
                    <label> </label>
                    <input
                        type="text"
                        name="pickupInstructions"
                        value={formData.pickupInstructions}
                        onChange={handleInputChange}
                        placeholder="   ()"
                    />
                </div>

                <div className="form-group">
                    <label> </label>
                    <input
                        type="text"
                        name="deliveryInstructions"
                        value={formData.deliveryInstructions}
                        onChange={handleInputChange}
                        placeholder="   ()"
                    />
                </div>

                {estimatedPrice > 0 && (
                    <div style={{
                        padding: '15px',
                        backgroundColor: '#e8f5e8',
                        border: '1px solid #4caf50',
                        borderRadius: '4px',
                        marginBottom: '20px'
                    }}>
                        <strong> : {formatSYP(estimatedPrice)}</strong>
                        <br />
                        <small> : {formatSYP(Math.floor(estimatedPrice * 0.70))}</small>
                    </div>
                )}

                <button
                    type="submit"
                    className="btn btn-success"
                    disabled={isSubmitting}
                    style={{ width: '100%', padding: '12px' }}
                >
                    {isSubmitting ? ' ...' : '  '}
                </button>
            </form>
        </div>
    );
};

export default SendPackage;