import React, { useState } from 'react';
import { deliveryAPI } from '../services/deliveryAPI';
import { PARCEL_SIZES, DELIVERY_MODES, DEMO_USERS } from '../utils/constants';

const DemoDataGenerator = () => {
    const [isGenerating, setIsGenerating] = useState(false);
    const [generatedCount, setGeneratedCount] = useState(0);

    const sampleDeliveries = [
        {
            senderId: DEMO_USERS.SENDER.id,
            parcelSize: PARCEL_SIZES.SMALL,
            deliveryMode: DELIVERY_MODES.DOOR_TO_DOOR,
            pickupAddress: ' -   -   45',
            deliveryAddress: ' -   -   ',
            description: ' ',
            pickupInstructions: '  -   8',
            deliveryInstructions: '  '
        },
        {
            senderId: DEMO_USERS.SENDER.id,
            parcelSize: PARCEL_SIZES.MEDIUM,
            deliveryMode: DELIVERY_MODES.DOOR_TO_DOOR,
            pickupAddress: ' -  -   -  ',
            deliveryAddress: ' -   -   23',
            description: ' ',
            pickupInstructions: '  ',
            deliveryInstructions: '  '
        },
        {
            senderId: DEMO_USERS.SENDER.id,
            parcelSize: PARCEL_SIZES.LARGE,
            deliveryMode: DELIVERY_MODES.DOOR_TO_DOOR,
            pickupAddress: ' -  -   -   ',
            deliveryAddress: ' -   -   12',
            description: '  ',
            pickupInstructions: '   9   8 ',
            deliveryInstructions: '    '
        }
    ];

    const generateDemoDeliveries = async () => {
        setIsGenerating(true);
        setGeneratedCount(0);

        try {
            for (let i = 0; i < sampleDeliveries.length; i++) {
                await deliveryAPI.createDelivery(sampleDeliveries[i]);
                setGeneratedCount(i + 1);
                // Add small delay between requests
                await new Promise(resolve => setTimeout(resolve, 500));
            }

            alert('  3    !');
        } catch (error) {
            alert('    : ' + error.message);
        } finally {
            setIsGenerating(false);
        }
    };

    return (
        <div style={{
            margin: '20px 0',
            padding: '15px',
            border: '2px dashed #3498db',
            borderRadius: '8px',
            backgroundColor: '#f8f9fa'
        }}>
            <h4 style={{ margin: '0 0 10px 0', color: '#2c3e50' }}>
                   
            </h4>
            <p style={{ fontSize: '14px', color: '#666', margin: '0 0 15px 0' }}>
                 3       
            </p>

            <button
                className="btn btn-success"
                onClick={generateDemoDeliveries}
                disabled={isGenerating}
                style={{
                    padding: '12px 24px',
                    fontSize: '16px',
                    fontWeight: 'bold'
                }}
            >
                {isGenerating ?
                    ` ... (${generatedCount}/3)` :
                    '   '
                }
            </button>

            {isGenerating && (
                <div style={{
                    marginTop: '10px',
                    fontSize: '12px',
                    color: '#27ae60'
                }}>
                       {generatedCount}  3 
                </div>
            )}
        </div>
    );
};

export default DemoDataGenerator;